@font-face {
    font-family: 'icomoon';
    src:    url('../fonts/icomoon.eot');
    src:    url('../fonts/icomoon.eot') format('embedded-opentype'),
        url('../fonts/icomoon.woff2') format('woff2'),
        url('../fonts/icomoon.ttf') format('truetype'),
        url('../fonts/icomoon.woff') format('woff'),
        url('../fonts/icomoon.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

/* use !important to prevent issues with browser extensions that change fonts */
 .icon {
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &.maximize:before { content: "\e91f"; }
    &.minimize:before { content: "\e920"; }
    &.download:before { content: "\e91d"; }
    &.eye:before { content: "\e91e"; }
    &.checkmark:before { content: "\e918"; }
    &.search:before { content: "\e916"; }
    &.duplicate:before { content: "\e917"; }
    &.remove:before { content: "\e909"; }
    &.plus:before { content: "\e90a"; }
